export async function router(event) {
  const { namespace } = event;

  let apiResult = { namespace, api: {} };
  try {
    const res = await fetch(`http://localhost:3000/api/v1/${namespace}`);
    apiResult.api = await res.json();
  } catch (err) {
    apiResult.api.err = err;
    console.log(err);
  }

  return apiResult;
}
