import React, { useEffect, useState } from "react";
import { useContext, useCallback } from "react";
import { NotificationContext } from "./../contexts";
import { Notification } from "./../components/Notification/Notification";

export function useNotificationError(err) {
  const [error, setError] = useState(err);
  const setNotification = useContext(NotificationContext);
  const addNotification = useCallback(notification => {
    const identifier = Date.now() + Math.floor(Math.random() * 100000000);
    const elm = React.cloneElement(notification, { identifier });
    setNotification(prev => ({ list: [...prev.list, elm] }));
  }, []);

  useEffect(() => {
    if (error) {
      addNotification(
        <Notification severity="error" message={error.message} />
      );
    }
  }, [error]);

  useEffect(() => {
    if (err && err !== error) {
      setError(err);
    }
  }, [err, error]);

  return { setError };
}
